/* Modern Profile Page Styles */

.profile-main-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem 1rem;
  background: #fff;
  border-radius: 1.5rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* User Info Card */
.profile-user-card {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
.profile-avatar {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #3b82f6;
  box-shadow: 0 2px 8px rgba(59,130,246,0.08);
}
.profile-user-details h2 {
  font-size: 2rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
}
.profile-email {
  color: #6b7280;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.profile-bio {
  color: #374151;
  font-size: 1.05rem;
  margin-top: 0.5rem;
}

/* Quick Stats Bar */
.profile-stats-bar {
  display: flex;
  justify-content: space-around;
  background: #f3f4f6;
  border-radius: 1rem;
  padding: 1rem 0;
  margin-bottom: 2rem;
}
.profile-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile-stat-icon {
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
}
.profile-stat-value {
  font-size: 1.25rem;
  font-weight: 700;
  color: #2563eb;
}
.profile-stat-label {
  color: #6b7280;
  font-size: 0.95rem;
}

/* Create Tour Button */
.profile-create-tour {
  text-align: center;
  margin-bottom: 2.5rem;
}
.profile-create-tour-btn {
  background: linear-gradient(90deg, #3b82f6 0%, #7c3aed 100%);
  color: #fff;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.9rem 2.2rem;
  border: none;
  border-radius: 0.75rem;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(59,130,246,0.08);
  transition: background 0.2s, transform 0.2s;
}
.profile-create-tour-btn:hover {
  background: linear-gradient(90deg, #2563eb 0%, #7c3aed 100%);
  transform: translateY(-2px) scale(1.03);
}

/* Recent Activity */
.profile-recent-activity {
  margin-bottom: 2.5rem;
}
.profile-recent-activity h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #1f2937;
}
.profile-recent-cards {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.profile-recent-card {
  background: #f9fafb;
  border-radius: 1rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
  padding: 1rem;
  flex: 1 1 220px;
  min-width: 220px;
  display: flex;
  gap: 1rem;
  align-items: center;
}
.profile-recent-img-wrap {
  width: 70px;
  height: 70px;
  border-radius: 0.75rem;
  overflow: hidden;
  flex-shrink: 0;
}
.profile-recent-img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.profile-recent-empty {
  color: #9ca3af;
  font-size: 1rem;
  padding: 1.2rem 0;
}

/* Tabs */
.profile-tabs {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.profile-tab-btn {
  background: none;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  color: #6b7280;
  padding: 0.7rem 1.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
  cursor: pointer;
  transition: color 0.2s, background 0.2s;
}
.profile-tab-btn.active, .profile-tab-btn:hover {
  color: #3b82f6;
  background: #f3f4f6;
}

/* Tab Content */
.profile-tab-content {
  background: #f9fafb;
  border-radius: 0 0 1rem 1rem;
  padding: 2rem 1.5rem;
  min-height: 180px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}
.profile-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.profile-list-item {
  padding: 1rem 0;
  border-bottom: 1px solid #e5e7eb;
}
.profile-list-item:last-child {
  border-bottom: none;
}
.profile-empty {
  color: #9ca3af;
  text-align: center;
  font-size: 1.1rem;
  padding: 2rem 0;
}

/* Loading & Error */
.profile-loading, .profile-error {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #6b7280;
}
.profile-spinner {
  border: 4px solid #e0e7ef;
  border-top: 4px solid #3b82f6;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive */
@media (max-width: 700px) {
  .profile-main-container {
    padding: 1rem 0.2rem;
  }
  .profile-user-card {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  .profile-stats-bar {
    flex-direction: column;
    gap: 1rem;
  }
  .profile-recent-cards {
    flex-direction: column;
    gap: 1rem;
  }
  .profile-tab-content {
    padding: 1rem 0.5rem;
  }
}

.read-more-btn {
  background: none;
  border: none;
  color: #3b82f6;
  font-weight: 600;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 0.5rem;
  padding: 0;
  transition: color 0.2s;
}
.read-more-btn:hover {
  color: #2563eb;
  text-decoration: underline;
}

.blog-modal-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(31, 41, 55, 0.45);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.blog-modal {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  max-width: 500px;
  width: 90vw;
  padding: 2rem 1.5rem 1.5rem 1.5rem;
  position: relative;
  animation: modalIn 0.2s;
}
@keyframes modalIn {
  from { transform: translateY(40px) scale(0.98); opacity: 0; }
  to { transform: translateY(0) scale(1); opacity: 1; }
}
.blog-modal-close {
  position: absolute;
  top: 1rem;
  right: 1.2rem;
  background: none;
  border: none;
  font-size: 2rem;
  color: #6b7280;
  cursor: pointer;
  transition: color 0.2s;
}
.blog-modal-close:hover {
  color: #ef4444;
}
.blog-modal h2 {
  margin-top: 0;
  font-size: 1.4rem;
  font-weight: 700;
  color: #1f2937;
}
.blog-modal p {
  color: #374151;
  font-size: 1.05rem;
  margin-top: 1rem;
  white-space: pre-line;
}

.logout-btn {
  background: linear-gradient(90deg, #ef4444 0%, #f59e42 100%);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 0.5rem;
}

.logout-btn:hover {
  background: linear-gradient(90deg, #dc2626 0%, #f59e42 100%);
  transform: translateY(-1px);
}